/* Remove padding and margin */
html, body {
  margin: 0;
  padding: 0;
  border: 0;
  height: 100%;
  width: 100%;
}

body {
  font: normal 16px/1.5 "Roboto", sans-serif;
  color: #333;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li {
  display: inline-block;
  margin-right: 15px;
}

ul li:last-child {
  margin-right: 0;
}

.alert-icon {
  float: left;
  margin-right: 15px;
}

.alert-info {
  margin: 0;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 14px;
}

.alert-container {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
}

.close-icon {
  float: right;
  color: #000;
  margin-top: 0;
  margin-left: 0;
  width: 21px;
  height: 21px;
  position: relative;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  text-indent: -999px;
  overflow: hidden;
  white-space: nowrap;
}

.close-icon:before, .close-icon:after {
  content: '';
  position: absolute;
  top: 8px;
  width: 15px;
  height: 2px;
  left: 0;
}

.close-icon:before {
  background-color: #fff;
  transform: rotate(-45deg);
}

.close-icon:after {
  background-color: #fff;
  transform: rotate(45deg);
}

.alert {
  background-color: #29d2e4;
  border: 1px solid #29d2e4;
  color: #fff;
  padding: 15px 20px;
}

.alert.alert-custom {
  background-color: #000;
  border: 1px solid #000;
}

.alert.alert-info {
  background-color: #29d2e4;
  border: 1px solid #29d2e4;
}

.alert.alert-success {
  background-color: #8bc34a;
  border: 1px solid #8bc34a;
}

.alert.alert-warning {
  background-color: #f87d09;
  border: 1px solid #f87d09;
}

.alert.alert-danger {
  background-color: #e91e63;
  border: 1px solid #e91e63;
}

