.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown .caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 8px dashed;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}

#dropdown-menu {
  position: absolute;
  top: 100%;
  background: #fff;
  box-shadow: 1px 1px 4px 2px rgba(51, 51, 51, 0.2);
  overflow: hidden;
  border-radius: 6px;
  list-style: none;
  margin: 5px 0 0 0;
  padding: 0;
  width: 200px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease-in-out;
  z-index: 10;
}

#dropdown-menu.open {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease-in-out;
}

#dropdown-menu li:hover {
  background: #e6e6e6;
}

#dropdown-menu a {
  display: block;
  padding: 10px;
  color: #000;
  text-decoration: none;
}

@-webkit-keyframes flash {
  0%, 30%, 100% {
    color: #000;
    background: #EEE;
  }
  15% {
    color: #FFF;
    background: #3498DB;
  }
}

@-moz-keyframes flash {
  0%, 30%, 100% {
    color: #000;
    background: #EEE;
  }
  15% {
    color: #FFF;
    background: #3498DB;
  }
}

@keyframes flash {
  0%, 30%, 100% {
    color: #000;
    background: #EEE;
  }
  15% {
    color: #FFF;
    background: #3498DB;
  }
}

.flash {
  -webkit-animation-name: flash;
  -moz-animation-name: flash;
  animation-name: flash;
  -webkit-animation-duration: 600ms;
  -moz-animation-duration: 600ms;
  animation-duration: 600ms;
}

.select-dropdown {
