  text-align: left;
}

.select-dropdown .button:after {
  content: "";
  position: absolute;
  top: 46%;
  right: 10px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #fff;
}

.select-dropdown__list {
  margin: 5px 0 0 0;
  padding: 6px 0;
  list-style: none;
  position: absolute;
  top: 125%;
  left: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  border-radius: 3px;
  background: #fff;
  transition: all 0.25s ease-in-out;
  z-index: 10;
  box-shadow: 0 0 1px 1px rgba(193, 193, 193, 0.3);
}

.select-dropdown.is-open .select-dropdown__list {
