.parallax {
  display: block;
  height: auto;
  position: relative;
  width: auto;
}

.parallax__content {
  box-shadow: 0 32px 60px rgba(69, 77, 93, 0.3);
  height: auto;
  transform: perspective(1600px);
  transform-style: preserve-3d;
  transition: all .4s ease;
  width: 100%;
}

.parallax__content:before {
  content: "";
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  background: transparent;
  transition: all .4s ease;
  width: 100%;
}

.parallax__front {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  text-align: center;
  text-shadow: 0 0 3rem rgba(69, 77, 93, 0.95);
  top: 0;
  transform: translateZ(16px) scale(0.9);
  transition: all .4s ease;
  width: 100%;
  z-index: 1;
}

.parallax__front h2 {
  color: #fff;
}

.parallax__top--left {
  height: 50%;
  left: 0;
  position: absolute;
  top: 0;
  width: 50%;
  z-index: 300;
  cursor: pointer;
}

.parallax__top--left:hover ~ .parallax__content {
  transform: perspective(1600px) rotateX(-5deg) rotateY(5deg);
}

.parallax__top--left:hover ~ .parallax__content:before {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 50%);
